.list-item {
	padding: 10px 2%;
	font-size: 16px;
	h2 {
		line-height: 30px;
		border-bottom: 1px solid #eee;
	}
	p {
		line-height: 24px;
		padding: 0 5px;
		margin-top: 10px;
	}
	.btn {
		width: 100%;
		margin-top: 30px;
		-webkit-box-sizing: border-box;
		box-sizing: border-box;
	}
}